import { useFormik } from 'formik';
import * as Yup from 'yup';
import styles from './robotForm.module.css';

interface IFormValues {
  model: string;
  creator: string;
  email: string;
}


// *     ()
// .required('error text) -      
// .number() -    ,   
// .string() -  
// .min(100, 'error text') -   100 ()
// .max(1000, 'error text') -   1000
// .positive() -   
// .integer() -   

//  schema    Yup
const schema = Yup.object().shape({
  model: Yup
    .number()
    .typeError('    ')
    .required('  ! ')
    .integer(' -   !')
    .min(100, '     100!')
    .max(1000, '   !   1000...'),
  creator: Yup
    .string()
    .required('    '),
  email: Yup
    .string()
    .email('  email ')
    .required('email    ')
    .max(50, '50')
});

const RobotForm = () => {


  const formik = useFormik({
    initialValues: {
      model: '',
      creator: '',
      email: ''
    } as IFormValues,
    //    Yup
    validationSchema: schema,
    //      input
    validateOnChange: false,
    onSubmit: (values, {resetForm}) => {
      console.log(values);
      resetForm()
    }
  });


  return (
    <>
      <form onSubmit={formik.handleSubmit} className={styles.robotForm}>
        <label>   </label>
        <input value={formik.values.model} onChange={formik.handleChange} type="text" name='model' placeholder=' ' />
        <input value={formik.values.creator} onChange={formik.handleChange} type="text" name='creator' placeholder=' ' />
        <input value={formik.values.email} onChange={formik.handleChange} type="text" name='email' placeholder='email ' />
        <button type='submit'></button>
      </form>
      {/*      formik.errors */}
      <span className={styles.errors}>{formik.errors.model}</span>
      <span className={styles.errors}>{formik.errors.creator}</span>
      <span className={styles.errors}>{formik.errors.email}</span>
    </>
  );
};

export default RobotForm;