import MyButton from "../../components/forms/myButton/MyButton";
import "./lesson02.css";

function Lesson02() {
  //         return
  const heading = "Lesson 2. JSX";
  const classContainer = "all-container";

  const john = {
    name: "John",
    lastName: "Snow",
    symbol: "",
  };

  const rob = {
    name: "Rob",
    lastName: "Stark",
    symbol: "",
  };
  //                   
  function formatName(user) {
    return `  : ' ${user.name} ${user.lastName}', ${user.symbol}`;
  }
  //         
  const isLogin = true;
  //         
  const hasLicense = true;

  //    true  false   isLogin  
  if (!isLogin) {
    return (
      <div className={classContainer}>
        <h4 style={{ color: "red" }}>
          You are not authorized to see our lesson 
        </h4>
      </div>
    );
  }

  return (
    //          
    <div className={classContainer}>
      {/*      javascript       */}
      <h2>{heading}</h2>
      <h4 className="lesson2-list-heading"> React :</h4>
      <ul>
        <li>  JSX</li>
        <li>   </li>
        <li>  </li>
        <li> </li>
        <li> import / export</li>
        <li>   ,   </li>
      </ul>
      <h4>     JSX  </h4>
      <p>
          {john.name},    - {john.lastName} - {john.symbol}
      </p>
      <p> JSX    : {22 + 78} </p>
      <p>{formatName(rob)}</p>
      <p>{formatName(john)}</p>
      <p> {isLogin ? "" : ""}  </p>
      {hasLicense ? (
        <p>You can drive a car!  </p>
      ) : (
        <p>Don't use a car without license </p>
      )}
      {/*          */}
      {/* <p>{john}</p> */}
      <h4>    </h4>
      <MyButton />
      <MyButton />
      <MyButton />
    </div>
  );
}

export default Lesson02;
